import React from 'react';
import Joke from './Joke';
import {
  Jumbotron,
  Container,
  Row,
  Col
} from 'reactstrap';
import {cowjokes} from './jokes.js';

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      jokes: cowjokes
    }
  }

  render() {
    let jokeCards = this.state.jokes.map(joke => {
      return (
        <Col key={joke.id} className="p-3" sm="4">
          <Joke  joke={joke} />
        </Col>
      )
    })
    return (
      <div>
        <Jumbotron>
          <h1 className="display-3">Welcome to the Joke Store!</h1>
          <p className="lead">Specializing in cow jokes!</p>
          <hr className="my-2" />
          <p>Prepare to be amused!</p>
          <p className="lead">
          </p>
        </Jumbotron>
        <Container fluid>
          <Row>
            {jokeCards}
          </Row>
        </Container>
      </div>
    );
  }
};

